"use client";
import { useState } from "react";
import { uploadPDF, generateQuiz } from "../src/lib/api";
import { Upload, BookOpen, Play, Loader2, CheckCircle } from "lucide-react";

export default function Home() {
  // State Variables
  const [file, setFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadSuccess, setUploadSuccess] = useState(false);
  
  const [topic, setTopic] = useState("General Knowledge");
  const [difficulty, setDifficulty] = useState("Medium");
  const [questionCount, setQuestionCount] = useState(5);
  
  const [isLoadingQuiz, setIsLoadingQuiz] = useState(false);

  // 1. Handle File Upload
  const handleFileUpload = async () => {
    if (!file) return;
    setIsUploading(true);
    try {
      await uploadPDF(file);
      setUploadSuccess(true);
      alert("PDF Processed Successfully!");
    } catch (error) {
      console.error(error);
      alert("Upload Failed!");
    } finally {
      setIsUploading(false);
    }
  };

  // 2. Handle Quiz Generation
  const handleStartQuiz = async () => {
    setIsLoadingQuiz(true);
    try {
      const data = await generateQuiz(topic, difficulty, questionCount);
      console.log("Quiz Data:", data);
      
      // Store data in LocalStorage to pass to the next page (Simple method)
      localStorage.setItem("quizData", JSON.stringify(data.questions));
      
      // Navigate to Quiz Page (We will build this next)
      window.location.href = "/quiz"; 
      
    } catch (error) {
      console.error(error);
      alert("Failed to generate quiz. Did you upload a PDF?");
    } finally {
      setIsLoadingQuiz(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col items-center py-12 px-4">
      <div className="max-w-md w-full bg-white shadow-xl rounded-2xl p-8">
        
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-blue-600 flex justify-center items-center gap-2">
            <BookOpen /> AI Quiz Gen
          </h1>
          <p className="text-gray-500 mt-2">Upload notes, generate exams.</p>
        </div>

        {/* Step 1: Upload */}
        <div className="mb-8 p-4 border-2 border-dashed border-gray-200 rounded-xl hover:border-blue-400 transition">
          <label className="flex flex-col items-center cursor-pointer">
            <div className="bg-blue-50 p-3 rounded-full mb-2">
              {uploadSuccess ? <CheckCircle className="text-green-500" /> : <Upload className="text-blue-500" />}
            </div>
            <span className="text-sm font-medium text-gray-700">
              {file ? file.name : "Click to Upload PDF"}
            </span>
            <input 
              type="file" 
              accept=".pdf" 
              className="hidden" 
              onChange={(e) => setFile(e.target.files?.[0] || null)}
            />
          </label>
          {file && !uploadSuccess && (
            <button 
              onClick={handleFileUpload}
              disabled={isUploading}
              className="w-full mt-3 bg-black text-white py-2 rounded-lg text-sm font-medium hover:bg-gray-800 disabled:opacity-50"
            >
              {isUploading ? "Processing..." : "Process PDF"}
            </button>
          )}
        </div>

        {/* Step 2: Configure Quiz */}
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Topic</label>
            <input 
              type="text" 
              value={topic}
              onChange={(e) => setTopic(e.target.value)}
              className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 outline-none"
              placeholder="e.g. Thermodynamics"
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Difficulty</label>
              <select 
                value={difficulty}
                onChange={(e) => setDifficulty(e.target.value)}
                className="w-full p-2 border rounded-lg"
              >
                <option>Easy</option>
                <option>Medium</option>
                <option>Hard</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Questions</label>
              <select 
                value={questionCount}
                onChange={(e) => setQuestionCount(Number(e.target.value))}
                className="w-full p-2 border rounded-lg"
              >
                <option value={3}>3</option>
                <option value={5}>5</option>
                <option value={10}>10</option>
              </select>
            </div>
          </div>

          {/* Start Button */}
          <button 
            onClick={handleStartQuiz}
            disabled={isLoadingQuiz || !uploadSuccess}
            className="w-full bg-blue-600 text-white py-3 rounded-xl font-bold text-lg hover:bg-blue-700 transition flex justify-center items-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoadingQuiz ? (
              <><Loader2 className="animate-spin" /> Generating...</>
            ) : (
              <><Play fill="white" size={18} /> Start Quiz</>
            )}
          </button>
        </div>

      </div>
    </div>
  );
}