import axios from 'axios';

const api = axios.create({
  baseURL: 'http://localhost:8000', // Your Python Backend URL
});

export const uploadPDF = async (file: File) => {
  const formData = new FormData();
  formData.append('file', file);
  const response = await api.post('/upload', formData, {
    headers: { 'Content-Type': 'multipart/form-data' },
  });
  return response.data;
};

export const generateQuiz = async (topic: string, difficulty: string, count: number) => {
  const response = await api.post('/generate_quiz', {
    topic,
    difficulty,
    count,
  });
  return response.data; // Returns { questions: [...] }
};

export default api;